//layout mixins - start

@mixin edgtfTableLayout() {
    position: relative;
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
}

@mixin edgtfTableCellLayout($vertical-align: middle) {
    position: relative;
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin edgtfRelativeHolderLayout($vertical-align: middle) {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin edgtfAbsoluteHolderLayout() {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@mixin edgtfTypographyLayout() {
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    font-style: inherit;
    line-height: inherit;
    letter-spacing: inherit;
    text-transform: inherit;
}

//layout mixins - end

//transition mixins - start

@mixin edgtfTransition($transition-param...) {
    -webkit-transition: $transition-param;
    -moz-transition: $transition-param;
    transition: $transition-param;
}

@mixin edgtfTransitionTransform($transition-param...) {
    -webkit-transition: -webkit-transform $transition-param;
    -moz-transition: -moz-transform $transition-param;
    transition: transform $transition-param;
}

@mixin edgtfTransform($transform-param...) {
    -webkit-transform: $transform-param;
    -moz-transform: $transform-param;
    transform: $transform-param;
}

@mixin edgtfAnimation($animation-param...) {
    -webkit-animation: $animation-param;
    -moz-animation: $animation-param;
    animation: $animation-param;
}

@mixin edgtfTransformOrigin($animation-param...) {
    -webkit-transform-origin: $animation-param;
    -moz-transform-origin: $animation-param;
    transform-origin: $animation-param;
}

//transition mixins - end

//checkbox mixins - start

$checkbox-size: 15px;
$checkbox-border-width: 1px;
%checkbox-style {
    position: relative;
    margin: 8px 0;
    line-height: 1;
    input[type=checkbox] {
        width: $checkbox-size;
        height: $checkbox-size;
        max-height: $checkbox-size;
        position: relative;
        display: inline-block;
        vertical-align: top;
        top: 0;
        left: 0;
        margin: 0;
    }
    input[type=checkbox] + label {
        display: inline-block;
        pointer-events: none;
        cursor: pointer;;
        position: absolute;
        top: 0;
        left: 0;
        line-height: 0;
    }
    input[type=checkbox] + label span.edgtf-label-text {
        padding-left: 10px;
        display: inline-block;
        line-height: $checkbox-size;
        color: $default-heading-color;
    }
    input[type=checkbox] + label .edgtf-label-view {
        display: inline-block;
        width: $checkbox-size;
        height: $checkbox-size;
        box-sizing: border-box;
        vertical-align: top;
        cursor: pointer;
        border: $checkbox-border-width solid $default-border-color;
        background-color: $default-background-color;
        border-radius: 2px;
        &:hover {
            cursor: pointer;
        }
    }
    input[type=checkbox] + label .edgtf-label-view:after {
        content: '';
        width: $checkbox-size / 2 - $checkbox-border-width;
        height: $checkbox-size / 2 - $checkbox-border-width;
        position: absolute;
        top: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        left: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        background-color: $first-main-color;
        opacity: 0;
        @include edgtfTransition(opacity 0.3s ease-in-out);
    }
    input[type=checkbox]:checked + label .edgtf-label-view:after {
        opacity: 1;
    }
}

//checkbox mixins - end

/* common mixins - start */

@mixin edgtfBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin edgtfImageOverlayHoverStyle($with-hover: true) {

    @if ($with-hover) {

        &:hover {

            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include edgtfAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-heading-color, .02);
            opacity: 0;
            @include edgtfTransition(opacity .02s);
        }

    } @else {
        @include edgtfAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;
        @include edgtfTransition(opacity .4s);
    }
}

@mixin edgtfStandardPaginationStyle($list_type: null) {
	@include edgtfRelativeHolderLayout(top);
	margin: 34px 0 52px;
	clear: both;
	
	ul {
		@include edgtfRelativeHolderLayout(top);
		padding: 0;
		margin: 0;
		list-style: none;
		text-align: center;
		
		li {
			position: relative;
			display: inline-block;
			vertical-align: top;
			margin: 0 15px;
			
			a {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0;
				padding: 0;
                font-size: 17px;
                color: rgba(255,255,255,.7);

                &:after {
                    content: '';
                    position: absolute;
                    bottom: -15px;
                    left: -8px;
                    height: 5px;
                    width: 26px;
                    background-color: $first-main-color;
                    opacity: 0;
                    -webkit-transition: opacity .2s ease;
                    -moz-transition: opacity .2s ease;
                    transition: opacity .2s ease;
                    -webkit-transform: skewX(15deg);
                    -moz-transform: skewX(15deg);
                    transform: skewX(15deg);
                }

                &:hover {
                    &:after {
                        opacity: 1;
                    }
                }
			}
			
			&.edgtf-pag-active {

                a:after {
					opacity: 1;
				}
			}
			
			&.edgtf-pag-prev,
			&.edgtf-pag-next,
			&.edgtf-pag-first,
			&.edgtf-pag-last {
				margin: 0 2px;
				
				a {
					font-size: 24px;
                    color: #fff;
					
					span {
						display: block;
						line-height: inherit;
						
						&:before {
							display: block;
							line-height: inherit;
						}
					}

                    &:after {
                        display: none;
                    }
				}
			}
			
			@if ($list_type == 'shortcode') {
				
				&.edgtf-pag-prev {
					
					a {
						opacity: 0;
					}
				}
				
				&.edgtf-pag-next {
					
					a {
						opacity: 1;
					}
				}
				
			} @else if ($list_type == 'shop') {
				
				span {
					position: relative;
					display: inline-block;
					vertical-align: top;
					margin: 0;
					padding: 0;
                    color: $default-text-color;
                    font-size: 17px;

                    &.current {
                        color: $default-text-color;

                        &:after {
                            content: '';
                            position: absolute;
                            bottom: -21px;
                            left: -8px;
                            height: 5px;
                            width: 26px;
                            background-color: $first-main-color;
                            opacity: 1;
                            -webkit-transition: opacity .2s ease;
                            -moz-transition: opacity .2s ease;
                            transition: opacity .2s ease;
                            -webkit-transform: skewX(15deg);
                            -moz-transform: skewX(15deg);
                            transform: skewX(15deg);
                        }
                    }
				}
				
				a {
                    color: $default-text-color;

                    &:after {
                        bottom: -21px;
                    }
					
					&.next,
					&.prev {
						font-size: 0;
						line-height: 0;

                        &:after {
                            display: none;
                        }
						
						&:before {
							display: block;
							font-family: 'ElegantIcons'; // same icon pack as in our templates for pagination
							font-size: 24px;
							line-height: 26px;
							-webkit-font-smoothing: antialiased;
							-moz-osx-font-smoothing: grayscale;
						}
					}
					
					&.prev {
						margin-right: -9px;
						
						&:before {
							content: "\44";
						}
					}
					
					&.next {
						margin-left: -9px;
						
						&:before {
							content: "\45";
						}
					}
				}
			}
		}
	}
}

@mixin edgtfButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: inherit;
    font-size: 13px;
    line-height: 2em;
    letter-spacing: 0.06em;
    font-weight: 700;
    text-transform: uppercase;
    border-radius: 0;
    outline: none;
    box-sizing: border-box;
    @include edgtfTransition(all .2s ease-in-out);
}

@mixin edgtfButtonSize($size: medium) {
    
    @if ($size == 'small') {
        padding: 11px 36px;
        
    } @else if ($size == 'medium') {
        padding: 13px 44px;
        
    } @else if ($size == 'large') {
        padding: 13px 54px;
        
    } @else if ($size == 'huge') {
        display: block;
        text-align: center;
        padding: 13px 26px;
    }
}

@mixin edgtfButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin edgtfButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border: 1px solid transparent $important;
}

@mixin edgtfButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
}

@mixin edgtfButtonOutlineColor($important: null) {
    color: $first-main-color $important;
    background-color: transparent $important;
    border: 1px solid $first-main-color $important;
}

@mixin edgtfButtonOutlineHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border-color: $first-main-color $important;
}

@mixin edgtfPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }
    &:-moz-placeholder {
        @content;
        opacity: 1;
    }
    &::-moz-placeholder {
        @content;
        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

/* common mixins - end */

/* mixins styles - start */

%input-style {
    position: relative;
    width: 100%;
    margin: 0 0 10px;
    padding: 16px 25px;
    font-family: $default-text-font;
    font-size: 17px;
    line-height: 20px;
    font-weight: inherit;
    color: $default-heading-color;
    background-color: rgba(255,255,255,.05);
    border: 1px solid transparent;
    border-radius: 0;
    outline: 0;
    -webkit-appearance: none;
    cursor: text;
    box-sizing: border-box;
    @include edgtfTransition(border-color 0.2s ease-in-out);

    &:focus {
        border-color: transparent;
        border-bottom-color: $first-main-color;
    }

    @include edgtfPlaceholder {
        color: inherit;
    }
}

/* mixins styles - end */

